<template>
  <div class="custom-tooltip-container">
    <div @mouseover="showTooltip" @mouseout="hideTooltip">
      <slot></slot>
    </div>
    <div v-if="tooltipVisible" class="custom-tooltip"
      :class="{ 'top': position === 'top', 'right': position === 'right', 'bottom': position === 'bottom', 'left': position === 'left' }">
      <p>{{ text }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tooltipVisible: false,
      position: 'left' // Pode ser 'top', 'right', 'bottom' ou 'left'
    };
  },
  props: {
    text: String,
  },
  methods: {
    showTooltip(event) {
      // Lgica para determinar a posio com base na posio do boto em relao  janela
      const buttonRect = event.target.getBoundingClientRect();
      const viewportHeight = window.innerHeight;

      if (buttonRect.top > viewportHeight / 2) {
        this.position = 'top';
      } else {
        this.position = 'bottom';
      }

      // Adiciona um delay de 0.5 segundos (500 milissegundos)
      setTimeout(() => {
        this.tooltipVisible = true;
      }, 500);
    },
    hideTooltip() {
      this.tooltipVisible = false;
    }
  }
};
</script>

<style scoped>
.custom-tooltip-container {
  position: relative;
  display: flex;
}

.custom-tooltip {
  position: absolute;
  background-color: #447EF7 !important;
  color: #fff;
  padding: 5px 13px 5px 13px;
  border-radius: 5px;
  display: block;
  transition: opacity 0.3s ease-in-out;
  /* Adiciona uma transio de opacidade */
  opacity: 0;
  /* Inicializa com opacidade 0 */
  width: 90px;
  /* Define uma largura mxima para evitar que o tooltip se expanda demais */
  text-align: center;
  /* Alinha o texto ao centro */
  word-wrap: break-word;
  overflow: hidden;
  /* Impede que o texto transborde do tooltip */
  text-overflow: ellipsis;
  /* Adiciona reticncias (...) ao texto que transborda */
  font-size: 12px;
  font-family: 'Poppins-Medium';
}

.custom-tooltip p {
  margin: 0;
  /* Remove margens padro do pargrafo */
}

.custom-tooltip.top,
.custom-tooltip.bottom {
  left: 20%;
  top: -100%;
  transform: translateX(-100%);
}

.custom-tooltip.left,
.custom-tooltip.right {
  top: 50%;
  left: -100%;
  transform: translateY(-50%);
}

.custom-tooltip-container:hover .custom-tooltip {
  display: block;
  opacity: 1;
  /* Ajusta a opacidade para 1 ao passar o mouse */
}
</style>