import Clipboard from 'clipboard';

export function copyToClipboard(selector, text) {
    var textData = {
        type: "",
        message: ""
    };

    const clipboard = new Clipboard(selector, {
        text: () => text,
    });

    clipboard.on('success', () => {
        textData.message = 'Copiado para a rea de transferncia com sucesso!';
        textData.type="alert-success",
        clipboard.destroy();
    });

    clipboard.on('error', () => {
        textData.message = 'Erro ao copiar para a rea de transferncia!';
        textData.type = "alert-danger";
        clipboard.destroy();
    });

    clipboard.onClick({ currentTarget: document.querySelector(selector)});
    return textData;
}
