<template>
    <nav class="navbar navbar-expand-lg navbar-light navbar-top navbar-left" :class="navHide">
        <div class="container-fluid">
            <div class="navbar-brand">
                <span class="navbar-text">
                    <p id="nav-welcome">Ol, sejam bem-vindos!</p>
                    <p id="nav-consult">Consulte nossos sistemas, sites e cursos.</p>
                </span>
            </div>

            <!--Contedo da Direita-->
            <div class="collapse navbar-collapse">
                <div class="navbar-nav ms-auto">
                    <form class="row">
                        <div class="col-md-auto">
                            <div class="input-group">
                                <input type="number" v-model="mbps" placeholder="Plano Mbps" class="form-control form-input">
                                <button @click="responseMbpsFunction" type="button" class="input-group-text btn-md-input"><i class="bi bi-search"></i></button>
                            </div>
                        </div>

                        <div class="col-md-auto">
                            <div class="input-group">
                                <input type="text" v-model="IP" @input="validationIP" placeholder="IP Localizao" class="form-control form-input">
                                <button @click="responseGeoIPFunction" type="button" class="input-group-text btn-md-input"><i class="bi bi-search"></i></button>
                            </div>
                        </div>

                        <div class="col-md-auto">
                            <div class="input-group">
                                <input v-model="ports" type="text" placeholder="Porta TCP / UDP" class="form-control form-input">
                                <button @click="responsePortFunction" type="button" class="input-group-text btn-md-input"><i class="bi bi-search"></i></button>
                            </div>
                        </div>

                        <div class="col-md-auto">
                            <div class="input-group">
                                <input v-model="macAddress" @input="formatMacAddressFunction" type="text" class="form-control form-input" placeholder="Padro MAC">
                                <button @click="copyMacAddressFunction" type="button" id="btn-copiar" class="input-group-text btn-md-input"><i class="bi bi-files"></i></button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            <!--Contedo da Direita Fim-->
        </div>
    </nav>
</template>

<script>
import {Mbps} from '@/assets/model/Mbps-model';
import { copyToClipboard } from "../assets/controller/clipboard-Controller";
import {geoIP} from "@/assets/api/ipinfo-api";
import { checkPorts } from '@/assets/controller/checkPort-Controller';


export default {
    name: 'NavBar',
    data() {
        return {
            mbps : null,
            Mbps: null,
            macAddress: '',
            ports: '',
            IP : '',
            erroIP :false
        };
    },
    props: {
        navHide: String,
    },
    methods: {

        responseMbpsFunction(){
            this.Mbps = new Mbps(this.mbps);    
            this.$emit("eventTextModal",this.Mbps.getFlat);
        },

        async responseGeoIPFunction(){
           if(this.geoIP != ''){
            this.$emit("eventTextModal",await geoIP(this.IP));
           }else{
            this.$emit("eventTextModal","No foi possvel encontrar as informaes");
           }
        },
        responsePortFunction() {
          this.$emit("eventTextModal", checkPorts(this.ports));
        },

        formatMacAddressFunction() {
            if (this.macAddress) {
                this.macAddress = this.macAddress.replace(/:/g, '').toUpperCase();
            }
        },

        copyMacAddressFunction() {
            var alert = copyToClipboard('#btn-copiar', this.macAddress);
            this.$emit('eventShowAlert', { type: alert.type, message: alert.message });
        },

        validationIP() {
         // Expresso regular para validar um endereo IP (IPv4 ou IPv6)
         const regexIP = /^([0-9a-fA-F]{1,4}:){7,7}[0-9a-fA-F]{1,4}|^(([0-9a-fA-F]{1,4}:){1,6}:|:)(([0-9a-fA-F]{1,4}:){1,6}:)?[0-9a-fA-F]{1,4}$/;
         // Verifica se o valor do endereo IP corresponde  expresso regular
         this.erroIP = !regexIP.test(this.IP);
    },
    }
};
</script>

