<template>
  <div id="id-carousel" class="carousel" @mouseenter="showControls = true" @mouseleave="showControls = false">
    <div class="carousel-inner image-container" @mousemove="magnify" @mouseenter="showLens" @mouseleave="hideLens">
      <div v-for="(image, index) in equipments.imgs" :key="image.id" :class="{ 'carousel-item': true, active: index === currentIndex }">
        <img class="carousel-image" :class="image.box" v-if="image.src" :src="loadImgFunction(image.src)" :id="'image-' + index" :ref="'image-' + index" :alt="image.alt" />
      </div>
      <div class="lens" ref="lens" :style="lensStyle"></div>
    </div>
    <a v-if="showControls" class="carousel-control-prev" @click.prevent="prevSlide" role="button" href="#">
      <span class="carousel-control-prev-icon bg-black" aria-hidden="true"></span>
      <span class="sr-only">Anterior</span>
    </a>
    <a v-if="showControls" class="carousel-control-next" @click.prevent="nextSlide" role="button" href="#">
      <span class="carousel-control-next-icon bg-black" aria-hidden="true"></span>
      <span class="sr-only">Prximo</span>
    </a>
  </div>
</template>

<script>
export default {
  props: {
    equipments: Object
  },
  data() {
    return {
      currentIndex: 0,
      showControls: false,
      lensStyle: {
        display: 'none',
        backgroundImage: '',
        backgroundPosition: '0% 0%',
        left: '0px',
        top: '0px',
        zIndex: '1' // Ajuste de z-index
      }
    }
  },
  mounted() {
    this.lens = this.$refs.lens;
  },
  methods: {
    nextSlide() {
      this.currentIndex = (this.currentIndex + 1) % this.equipments.imgs.length;
      this.updateImageSrc();
    },

    prevSlide() {
      this.currentIndex = (this.currentIndex - 1 + this.equipments.imgs.length) % this.equipments.imgs.length;
      this.updateImageSrc();
    },

    loadImgFunction(value) {
      try {
        return require(`@/assets/img/carousel/${value}`);
      } catch (error) {
        console.error(`Erro ao carregar imagem: ${error.message}`);
      }
    },

    updateImageSrc() {
      const imageRef = this.$refs[`image-${this.currentIndex}`][0];
      if (imageRef) {
        this.imageSrc = imageRef.src;
      }
    },

    magnify(e) {
      const imageRef = this.$refs[`image-${this.currentIndex}`][0];
      const imageRect = imageRef.getBoundingClientRect();

      const lensWidth = this.lens.offsetWidth;
      const lensHeight = this.lens.offsetHeight;

      let posX = e.clientX - imageRect.left - lensWidth / 2;
      let posY = e.clientY - imageRect.top - lensHeight / 2;

      if (posX < 0) posX = 0;
      if (posY < 0) posY = 0;
      if (posX > imageRect.width - lensWidth) posX = imageRect.width - lensWidth;
      if (posY > imageRect.height - lensHeight) posY = imageRect.height - lensHeight;

      const bgPosX = (posX / imageRect.width) * 150;
      const bgPosY = (posY / imageRect.height) * 150;

      this.lensStyle.backgroundImage = `url('${this.imageSrc}')`;
      this.lensStyle.backgroundPosition = `${bgPosX}% ${bgPosY}%`;
      this.lensStyle.left = `${posX}px`;
      this.lensStyle.top = `${posY}px`;
    },

    showLens() {
      this.updateImageSrc();
      this.lensStyle.display = 'block';
    },

    hideLens() {
      this.lensStyle.display = 'none';
    }
  }
}
</script>

