<template>
    <div class="alert-container">
        <transition name="fade">
            <!-- Se verdadeiro exibi o alert, recebe o tipo de alert e recebe o offset-->
            <div v-show="isFading" class="alert" :class="[typeMessage, offset]" role="alert">
                <div class="d-flex justify-content-between align-items-center">
                    <!--Adicionar contedo no objeto-->
                    <slot></slot>
                    <!--Fechar o alert-->
                    <button type="button" class="close alert-btn" @click="hideAlertFunction" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
   data(){
    return{
        isFading: false
    }
   },
   props:{
     typeMessage: String,
     alertHide: Boolean,
     offset: String
   },
   methods:{
      //Esconde alert
      hideAlertFunction(){
         this.isFading = !this.isFading;
      }
   },
   watch: {
    // varial alertHide na esculta.
    alertHide(newVal) {
      if (newVal) {
        // Inicia a transio de fade quando alertHide  verdadeiro
        this.isFading = true;
        //envia um evento para o objeto pai para alterar varivel para seu valor inicial
        this.$emit("eventAlertHide",this.alertHide);
        // Define um temporizador para desativar a transio aps um perodo de tempo (por exemplo, 1 segundo)
        setTimeout(() => {
          this.isFading = false;
        }, 5000);
      }
    },
  },
};
</script>

<style>

.alert-container{
  padding: 5px 5px 0px 5px;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9999; /* Certifique-se de que o z-index seja maior que outros elementos, se necessrio */
  bottom: 20px;
}

.alert {
  opacity: 1;
  transition: opacity 4s;
  font-size: 12px;
  font-family: 'Poppins-Regular';
  margin-top: 3.5%;
  margin-left: 5%;
}

.alert-btn{
  background-color: transparent;
  border: none;
  font-size: 14px;
}


.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>