<template>
  <!--Template-->
  <div id="app">
    <!--Contedo-->
    <main id="body">
      <!--Cabealho Principal-->
      <NavHeaderComponent :class="{ 'offset-2': tagHide ,'nav-ml-off1': !tagHide }"></NavHeaderComponent>
      <!--Cabealho Principal Fim-->

      <!--Principal-->
      <section id="content-body">
        <!--Recebe o tipo de alerta, recebe a varivel hideAlert como true, adiciona a classe offset-2 se false, esculta o evento e envia para a funo-->
        <AlertComponent :typeMessage = "objectAlert.type"  :alertHide="hideAlert" :class="{ 'offset-2': tagHide}" @eventAlertHide="hideAlertFunction">
          {{this.objectAlert.message}}
        </AlertComponent>

        <ModalComponent v-if="objectTextModal" :visible="true" @eventClose="hideModalFunction">
          <span class="modal-text text-justify" v-html="objectTextModal"></span>
        </ModalComponent>
        
        <AsideComponent @eventCliclkToggler="alterTagFunction"></AsideComponent>

        <!--Se tagHide for false recebe offset-02-->
        <!--eventShowAlert trs os dados e joga na funo showAlertFunction-->
        <NavBarComponent :class="{ 'offset-2': tagHide}" @eventShowAlert="showAlertFunction" @eventTextModal="showModalFunction"></NavBarComponent>
        <!--Div corpo-->
        <div class="row m-0 p-4">
          <div :class="{'col-md-9': !tagHide,'offset-1': !tagHide, 'col-md-8': tagHide, 'offset-2':tagHide}">
                <NavTabContentComponent></NavTabContentComponent>
                <NavTabEquipmentComponent></NavTabEquipmentComponent>
          </div>
          <!--Objeto com informaes dos setores de atendimento-->
          <NavTabsSetorComponent @eventShowAlert="showAlertFunction"></NavTabsSetorComponent>
        </div>
        <!--Div corpo fim-->
      </section>
      <!--Principal Fim-->

      <!--Rodap-->
      <footer class="text-center footer" :class="{ 'offset-1': tagHide } ">&copy; 2023 Brisanet Telecomunicaes SA. Todos os direitos reservados</footer>
      <!--Rodap Fim-->
    </main>
    <!--Contedo Fim-->
  </div>
  <!--Template Fim-->
</template>

<script>
import NavHeaderComponent from '@/components/NavHeaderComponent.vue';
import AsideComponent from '@/components/AsideComponent.vue';
import NavBarComponent from './components/NavBarComponent.vue';
import AlertComponent from './components/AlertComponent.vue';
import ModalComponent from './components/ModalComponent.vue';
import NavTabsSetorComponent from './components/NavTabsSetorComponent.vue';
import NavTabContentComponent from './components/NavTabContentComponent.vue';
import NavTabEquipmentComponent from './components/NavTabEquipmentComponent.vue';

export default {
  name: 'App',
  data() {
    return {
      tagHide: false,
      hideAlert: false,
      objectAlert: '',
      objectTextModal: ''
    }
  },
  components: {
    NavHeaderComponent,
    AsideComponent,
    NavBarComponent,
    AlertComponent,
    ModalComponent,
    NavTabsSetorComponent,
    NavTabContentComponent,
    NavTabEquipmentComponent,
},
  methods: {
    alterTagFunction(value) {
      this.tagHide = value;
    },

    showAlertFunction(objectvalue) {
      this.objectAlert = objectvalue;
      this.hideAlert = true;
    },

    hideAlertFunction(){
      this.hideAlert = false;
    },

    showModalFunction(value){
      this.objectTextModal = value;
    },

    hideModalFunction(){
      this.objectTextModal = '';
    }
  }
}
</script>

<style>
@import url('./assets/css/font.css');
@import url('./assets/css/input.css');
@import url('./assets/css/icon.css');
@import url('./assets/css/btn.css');

#body {
  display: flex;
  /*Deixar os componentes flexiveis*/
  min-height: 100vh;
  /*Minuto de altura (Body 100% da tela)*/
  flex-direction: column;
  /*componentes formato coluna*/
}

#content-body {
  flex: 1;
  background-color: #F3F5F7;
  position: relative;
}

.footer {
  color: #8891A1;
  font-size: 12px;
  font-family: 'Poppins-Regular';
  background-color: #F3F5F7;
  padding: 0 0 15px 0;
}
