export class Mbps {
    Mbps;
    download;
    upload;
    downloadKBps;
    uploadKBps;
    downloadMBps;
    uploadMBps
  
    constructor(Mbps) {
        this.Mbps = Mbps; // Adicionando o plano atual.
        this.download = this.Mbps; //Obtendo a entrega de 100% de Download.
        this.upload = (50/100) * this.Mbps; // Obtendo a entrega de 50% do Upload.
        this.downloadMBps = (this.download / 8).toFixed(1); // transferncia de dados em Mbps
        this.uploadMBps = (this.upload /8).toFixed(1); // transferncia dados em Mbps
        this.downloadKBps = (this.downloadMBps * 1024) / 8; // taxa de transferncia de dados Downlioad KBps.
        this.uploadKBps = (this.uploadMBps * 1024) / 8; // taxa de transferncia de dados Upload KBps.
      }
  
    get getMbps (){
        return this.Mbps;
    }
  
    get getDownloadGuarantee (){ 
        return (80/100) * this.download;
    }
  
    get getUpload (){
        return this.upload;
    }
  
    get getUploadGuarantee (){
        return (80/100) * this.upload;
    }
  
    get getDownloadKBps (){
      return this.downloadKBps;
    }
  
    get getUploadKBps(){
      return this.uploadKBps;
    }
  
    get getDownloadMbps (){
      return this.downloadMBps;
    }
  
    get getUploadMbps(){
      return this.uploadMBps;
    }

    get getFlat(){
      let value =
      `<span class='black-text'>Plano: ${this.getMbps} Mb/s (Megabits Por Segundo) Taxa de Transferncia.  </span><br><br>
      <span class='black-text'> Dowload 100 % : </span>
      <span class='gray-text'> ${this.getMbps} Mb/s (Megabits Por Segundo) Taxa de Transferncia.</span><br><br>
      <span class='black-text'> Garantia 80 % :</span>
      <span class='gray-text'> ${this.getDownloadGuarantee} Mb/s (Megabits Por Segundo) Taxa de Transferncia.</span><br>
      <span class='black-text'>Taxa de Transferncia De Dados Terico (Baixar).</span>
      <span class='gray-text'> = ${this.getDownloadKBps}  KB/s ou ${this.getDownloadMbps} MB/s. </span><br><br>
      <span class='black-text'> Upload 50% : </span>
      <span class='gray-text'>${this.getUpload} Mb/s (Megabits Por Segundo) Taxa de Transferncia.</span><br><br>
      <span class='black-text'> Garantia 80%: </span>
      <span class='gray-text'>${this.getUploadGuarantee} Mb/s (Megabits Por Segundo) Taxa de Transferncia.</span><br>
      <span class='black-text'>Taxa de Transferncia De Dados Terico (Enviar).</span>
      <span class='gray-text'> = ${this.getUploadKBps}  KB/s ou ${this.getUploadMbps} MB/s </span>`;
      return value;
    }
  }
