<template>
    <div class="col-md-3 mt-3">
        <div @click="isOpenFunction" class="card card-accordion">
            <a href="#" class="card-accordion-title d-flex align-items-center justify-content-center">
                <i class="btn-md-blue" :class="bnt_icon"></i>
                <span class="card-accordion-text">{{ title }} </span>
            </a>
        </div>

        <transition name="fade">
            <!--Se verdadeiro torne bloco e no, invisivel -->
            <div class="card-accordion-body" :class="{ 'd-block': isOpen, 'd-none': !isOpen }" >
                <slot></slot>
            </div>
        </transition>
    </div>
</template>

<script>
//Biblioteca para eventos
import mitt from 'mitt';

const emitter = mitt();

export default {
    name: "AccordionComponent",
    props: {
        title: String,
        bnt_icon: String,
    },
    data() {
        return {
            isOpen: false,
        }
    },
    methods: {
        //transforme em verdadeiro e passe a instnicia com evento
        isOpenFunction() {
            this.isOpen = !this.isOpen;
            emitter.emit("eventIsOpen", this);
        },

        closeAccordionFunction() {
            this.isOpen = false;
        },
    },
    //quando criar o obejeto esculte o evento
    created() {
        // Registrar um ouvinte para o evento personalizado
        emitter.on("eventIsOpen", (sourceAccordion) => {
            if (sourceAccordion !== this) {
                this.closeAccordionFunction();
            }
        });
    },
}
</script>

<style>

a {
    text-decoration: none !important;
}

/*Contedo do card accordion onde ficar todo o contedo do site*/
.card-accordion {
    transition: transform 0.5s ease;
}

.card-accordion:hover{
    transform: translateY(-5px);
}

.card-accordion-title {
    margin: 10px 0 9px 0;
    padding: 15px 5px 15px 5px;
}

.card-accordion-text {
    font-size: 12px;
    font-family: 'Poppins-SemiBold';
    margin-left: 10px;
    color: #333333;
}

.card-accordion-body{
    background-color: rgba(255, 255, 255, 0.605);
}
</style>