import portjson from '@/assets/json/port.json';

export function checkPorts(ports) {
    const openPorts = [];
    const filteredPorts = [];
    const closePorts = [];
    const dynamicPorts = [];
    const listPorts = ports.split(',').map(num => parseInt(num.trim()));
    const filteredNoc = [17,19,22,23,25,53,111,135,136,137,138,139,161, 369, 389, 445, 520, 521, 853, 953, 1900, 2700, 5353, 5900, 11211, 20005];
    const closeONU = [1900, 2222, 2323, 3389, 4145, 5353, 5555, 5900, 6789, 7547, 8291, 10001, 11211, 23231, 37215, 37777, 52869]; // Array de nmeros tambm bloqueados na ONU;
    let responseText = "";

    listPorts.forEach(port => {
        //Precisa de Ip Fixo
        if (port >= 1024 && port <= 49151) {
            openPorts.push(port);
        }
        // No precisa direcionar
        if (port >= 49152 && port <= 65355) {
            dynamicPorts.push(port);
        }
        //Portas Filtradas
        if ((port >= 135 && port <= 139) || filteredNoc.includes(port)) {
            filteredPorts.push(port);
        }
        // Bloqueadas na ONU
        if ((port >= 0 && port <= 1023) || closeONU.includes(port)) {
            closePorts.push(port);
        }
    });

    if (openPorts.length > 0) {
        responseText += `Porta (as) Registrada (as), Requisito -  IP Pblico, modo Bridge (ponte), roteador particular. Porta (as): <strong> ${openPorts.join(", ")} </strong><br><br>`;
    }

    if (filteredPorts.length > 0) {
        responseText += `Porta (as) Filtrada (as) (Filtered) pelo NOC. Solicitar liberao Farewall. Porta (as): <strong> ${filteredPorts.join(", ")} </strong><br><br>`;
    }

    if (closePorts.length > 0) {
        responseText += `Segurana da ONU - Porta (as) Fechada (as) (Closed). Porta (as): <strong> ${closePorts.join(", ")} </strong><br><br>`;
    }

    if (dynamicPorts.length > 0) {
        responseText += `Porta (as) No Necessita (am) Direcionar. Porta (as): <strong> ${dynamicPorts.join(", ")} </strong><br><br>`;
    }

    if (responseText === "") {
        responseText = "Porta (as) No Encontrada (as).";
    }
   
    if (listPorts.length > 0) {
        // Filtra o JSON para encontrar portas correspondentes
        const foundPorts = portjson.filter((item) => listPorts.includes(item['Port Number']));
        if (foundPorts.length > 0) {
            responseText += formatResponseText(foundPorts);
        }
    }
    return responseText;
}

function formatResponseText(foundPorts) {
    const uniquePortNumbers = new Set(); // Usamos um conjunto para garantir nmeros de porta nicos
    let formattedText = "";

    foundPorts.forEach(portData => {
        const portNumber = portData['Port Number'];

        // Verifica se o nmero de porta j foi processado
        if (!uniquePortNumbers.has(portNumber)) {
            // Ajusta o valor do protocolo para sempre ser "TCP e UDP"
            portData['Transport Protocol'] = "TCP / UDP";

            formattedText += Object.entries(portData)
                .map(([key, value]) => `${key}: ${value}`)
                .join('<br>'); // Adiciona quebra de linha HTML entre as informaes de cada porta
            formattedText += '<br><br>'; // Adiciona quebras de linha HTML adicionais para separar as portas
            uniquePortNumbers.add(portNumber); // Adiciona o nmero de porta ao conjunto para evitar duplicatas
        }
    });

    return formattedText += '<a href="https://www.iana.org/assignments/service-names-port-numbers/service-names-port-numbers.xhtml" target="_blank" style="text-decoration: none;">Iana.org</a>';
}
