<script>
export default {
    name: 'AsideComponent',
    data() {
        return {
            activeIndex: 0, // Definir o primeiro item como ativo por padro
            mouseOver: false,
            iconStates: {
                button1: true,
                button2: true,
                button3: true
            }
        };
    },
    computed: {
        iconClass() {
            // Esta computed property retorna uma funo que aceita um argumento buttonKey
            return buttonKey => {
                // Determina as classes do cone com base no estado do cone correspondente a esse boto
                return this.iconStates[buttonKey] ? 'icon-caret bi-caret-down-fill' : 'icon-caret bi-caret-up-fill';
                // Substitua as classes acima pelos nomes das classes reais do seu cone
            };
        }
    },
    methods: {
        activateItem(index) {
            this.activeIndex = index;
        },

        handleMouseOver() {
            this.mouseOver = true;
            this.$emit('eventCliclkToggler', this.mouseOver);
        },
        handleMouseLeave() {
            this.mouseOver = false;
            this.$emit('eventCliclkToggler', this.mouseOver);
        },
        togglerIcon(buttonKey) {
            // Inverte o estado do cone especfico para o boto fornecido
            this.iconStates[buttonKey] = !this.iconStates[buttonKey];
        },

        actionClick(buttonKey, value) {
            this.activateItem(value);
            this.togglerIcon(buttonKey);
        }
    }
}

</script>

