<template>
<div v-if="openClose" class="modal fade show">
  <div class="modal-dialog" >
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title"><i class="modal-icon bi bi-card-checklist"></i></h5>
      </div>
      <div class="modal-body">
        <slot class="modal-text"></slot>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-light" @click="openCloseFunction">&times;</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
 export default {
    name: 'ModalComponent',
    props:{
        visible: Boolean,
        modalTitle: String,
    },
    data(){
        return{
            openClose: this.visible,
        }
    },
    methods:{
        openCloseFunction(){
            this.openClose = !this.openClose;
            this.$emit('eventClose');
        }
    },
    watch:{
        visible: function(newVal){
            this.openClose = newVal;
        }
    }
 };
</script>

