<template>
    <ul @click="isOpenFunction" class="list-unstyled unstyled d-flex align-items-center">
        <li v-if="object.img" @click="togglerIcon('button1')" class="link-body d-flex justify-content-between align-items-center w-100">
            <span class="block-text">
                <img class="icon" :src="loadImgFunction(object.img)">
                <span class="link-sub-text">{{ object.title }}</span>
            </span>
            <i class=" link-icon" :class="iconClass('button1')"></i>
        </li>
    </ul>

    <transition name="fade">
        <!--Se verdadeiro torne bloco e no, invisivel -->
        <div :class="{ 'd-block': isOpen, 'd-none': !isOpen }">
            <slot></slot>
        </div>
    </transition>
</template>

<script>
//Biblioteca para eventos
import mitt from 'mitt';

const emitter = mitt();

export default {
    name: "AccordionSubComponent",
    props: {
        object : Object
    },
    data() {
        return {
            isOpen: false,
            iconStates: {
                button1: true,
                button2: true
            }
        }
    },
    computed: {
        iconClass() {
            // Esta computed property retorna uma funo que aceita um argumento buttonKey
            return buttonKey => {
                // Determina as classes do cone com base no estado do cone correspondente a esse boto
                return this.iconStates[buttonKey] ? 'icon-caret bi-caret-down-fill' : 'icon-caret bi-caret-up-fill';
                // Substitua as classes acima pelos nomes das classes reais do seu cone
            };
        }
    },
    methods: {
        //transforme em verdadeiro e passe a instnicia com evento
        isOpenFunction() {
            this.isOpen = !this.isOpen;
            emitter.emit("eventIsOpen", this);
        },

        closeAccordionFunction() {
            this.isOpen = false;
        },

        loadImgFunction(value) {
            try {
                return require(`@/assets/img/icon/${value}`);
            } catch (error) {
                // Em caso de erro, repasse a informao e continua
                console.error(`Erro ao carregar imagem: ${error.message}`);
            }
        },

        togglerIcon(buttonKey) {
            // Inverte o estado do cone especfico para o boto fornecido
            this.iconStates[buttonKey] = !this.iconStates[buttonKey];
        },
    },
    //quando criar o obejeto esculte o evento
    created() {
        // Registrar um ouvinte para o evento personalizado
        emitter.on("eventIsOpen", (sourceAccordion) => {
            if (sourceAccordion !== this) {
                this.closeAccordionFunction();
            }
        });
    },
}
</script>

<style>
/* herdou tudo de linkcomponent*/
.link-body{
    border-bottom: 1px solid rgb(200,200,200,0.2);
}
.block-text{
    margin-left: 30px;
}
.link-icon{
    font-size: 14px;
    margin-right: 30px;
    color: rgba(189, 192, 197, 0.8);
}

.link-sub-text{
  color: #333333;
  font-family: 'Poppins-Regular';
  font-size: 13px;
  transform: translateY(-50%);
  right: 10%;
  margin-left: 10px;
}
</style>