<template>
  <ul v-if="link.sublinks" class="list-unstyled">
    <li v-for="sublink in link.sublinks" :key="sublink.id" class="link-body">
      <span class="icon icon-img-unstyled" :class="classMargin">
        <i :class="sublink.icon"><img class="icon" v-if="sublink.img" :src="loadImgFunction(sublink.img)"></i>
      </span>
      <a class="link-text" :href="sublink.url" target="_blank">{{ sublink.text }}</a>
    </li>
  </ul>
</template>
  
<script>

export default {
  name: "LinkComponent",
  props: {
    link: Object,
    classMargin: String
  },
  methods:{
    loadImgFunction(value){
      try {
        return require(`@/assets/img/icon/${value}`);
      } catch (error) {
        // Em caso de erro, repasse a informao e continua
        console.error(`Erro ao carregar imagem: ${error.message}`);
      }
    }
  }
};
</script>

