export async function geoIP(geoIP){
    try {
        const url = `https://ipinfo.io/${geoIP}?token=256486b948bab5`;
        const response = await fetch(url);
        const data = await response.json();
        return "IP: "+data.ip+"<br>"+
        "Cidade: "+data.city+"<br>"+
        "Regio: "+data.region+"<br>"+
        "Orgo: "+data.org+"<br>"+
        "Localizao: "+data.loc+"<br>"+
        "Time: "+data.timezone+"<br>";
    } catch (error) {
        return'Erro ao obter informaes de geolocalizao:', error;
    }
}